import ForgeStep from "../ForgeStep";
import ForgeStepInput from "./ForgeStepInput";
import { Form, useForm } from '@mantine/form';

export default function ForgeStepForm() {

    interface FormValues {
        HIT_LIGHT:number,
        HIT_MEDIUM :number,
        HIT_HARD :number,
        DRAW :number,
        PUNCH :number,
        BEND :number,
        UPSET :number,
        SHRINK :number,
      }
    const form = useForm<FormValues>();
    return (
        <>
            <Form form={form}>
                {
                    Object.keys(ForgeStep).filter((v) => isNaN(Number(v))).map((step: any) => (
                        <ForgeStepInput step={step} formProps={form.getInputProps(step.toString())}/>
                    ))
                }
            </Form>
            <p>The calculated forge point is: 
                {
                    Object.keys(form.values).reduce(
                        (accumulator, key: string) => { 
                            return accumulator + Number(form.values[key as keyof FormValues] * ForgeStep[key as keyof typeof ForgeStep]);
                        }, 0
                    )
                    
                }</p>
        </>
    );
}