import { ActionIcon, Grid, Group, NumberInput, NumberInputHandlers, rem } from "@mantine/core";
import ForgeStep from "../ForgeStep";
import { useRef, useState } from "react";


export default function ForgeStepInput({ step, formProps }: { step: ForgeStep, formProps: any }) {
    const [value, setValue] = useState<number | ''>(0);
    const handlers = useRef<NumberInputHandlers>();


    return (
        <Grid>
            <Grid.Col span={4}>
                <p><b>{step}</b></p>
            </Grid.Col>
            <Grid.Col span={8}>
                <Group>
                    <ActionIcon size={42} variant="default" onClick={() => handlers?.current?.decrement()}>
                        -
                    </ActionIcon>
                    <NumberInput
                        id={step.toString()}
                        name={step.toString()}
                        defaultValue={0}
                        min={0}
                        max={150}
                        step={1}
                        handlersRef={handlers}
                        hideControls
                        value={value}
                        onChange={(val) => setValue(val)}
                        styles={{ input: { width: rem(54), textAlign: 'center' } }}
                        {...formProps}
                    />

                    <ActionIcon size={42} variant="default" onClick={() => handlers?.current?.increment()}>
                        +
                    </ActionIcon>
                </Group>
            </Grid.Col>
        </Grid>
    );
}